/textarea>\r\n                        <span >\r\n                            <img :src=\"data.image\" v-show=\"data.image != null\">\r\n                            <input type=\"file\" id=\"file\" @change=\"attachFile\">\r\n                        </span>\r\n                    </slot>\r\n                </div>\r\n                <div class=\"modal-footer\">\r\n                    <slot name=\"footer\">\r\n                        <button class=\"modal-default-button\" @click=\"uploadFile\">\r\n                            Finish\r\n                        </button>\r\n                    </slot>\r\n                </div>\r\n            </div>\r\n        </div>\r\n    </div>\r\n</template>\r\n\r\n\r\n<script>\r\n    export default {\r\n        props: ['product'],\r\n        data() {\r\n            return {\r\n                attachment: null\r\n            }\r\n        },\r\n        computed: {\r\n            data: function() {\r\n                if (this.product != null) {\r\n                    return this.product\r\n                }\r\n                return {\r\n                    name: \"\",\r\n                    units: \"\",\r\n                    price: \"\",\r\n                    description: \"\",\r\n                    image: false\r\n                }\r\n            }\r\n        },\r\n        methods: {\r\n            attachFile(event) {\r\n                this.attachment = event.target.files[0];\r\n            },\r\n            uploadFile(event) {\r\n                if (this.attachment != null) {\r\n                    var formData = new FormData();\r\n                    formData.append(\"image\", this.attachment)\r\n                    let headers = {'Content-Type': 'multipart/form-data'}\r\n                    axios.post(\"/api/upload-file\", formData, {headers}).then(response => {\r\n                        this.product.image = response.data\r\n                        this.$emit('close', this.product)\r\n                    })\r\n                } else {\r\n                    this.$emit('close', this.product)\r\n                }\r\n            }\r\n        }\r\n    }\r\n</script>\r\n\r\n<style scoped>\r\n    .modal-mask {\r\n        position: fixed;\r\n        z-index: 9998;\r\n        top: 0;\r\n        left: 0;\r\n        width: 100%;\r\n        height: 100%;\r\n        background-color: rgba(0, 0, 0, .5);\r\n        display: table;\r\n        transition: opacity .3s ease;\r\n    }\r\n    .modal-wrapper {\r\n        display: table-cell;\r\n        vertical-align: middle;\r\n    }\r\n    .modal-container {\r\n        width: 300px;\r\n        margin: 0px auto;\r\n        padding: 20px 30px;\r\n        background-color: #fff;\r\n        border-radius: 2px;\r\n        box-shadow: 0 2px 8px rgba(0, 0, 0, .33);\r\n        transition: all .3s ease;\r\n        font-family: Helvetica, Arial, sans-serif;\r\n    }\r\n    .modal-header h3 {\r\n        margin-top: 0;\r\n        color: #42b983;\r\n    }\r\n    .modal-body {\r\n        margin: 20px 0;\r\n    }\r\n    .modal-default-button {\r\n        float: right;\r\n    }\r\n    .modal-enter {\r\n        opacity: 0;\r\n    }\r\n    .modal-leave-active {\r\n        opacity: 0;\r\n    }\r\n    .modal-enter .modal-container,\r\n    .modal-leave-active .modal-container {\r\n        -webkit-transform: scale(1.1);\r\n        transform: scale(1.1);\r\n    }\r\n</style>"],"sourceRoot":""}]);
